import { createStyles, EM } from 'mic-global';
/**
 * A method to create property component styles
 * @param className A string represents the class name
 * @param styles An object represetns the component styles
 */
export const createPropertyComponentStyles = (className, style) => {
    return createStyles(className, Object.assign({ fontFamily: 'Menlo, monospace', lineHeight: EM.M5 }, style));
};
